/* 네비바 하단 고정 밑 세로 길이 설정 */
.nav-wrapper {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 6vh;
  background-color: #fff;
  padding: 1px 0px;
  box-shadow: 0 0px 10px rgba(0, 0, 0, 0.2);
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
}

/* nav태그 아래의 자식들을 수평정렬하기 위한 설정 */
nav {
  overflow: hidden;
  /* 네비게이션 바의 상단에 선 표현 */
  /* border-top: 2px solid rgba(128, 128, 128, 0.623); */
}
/* nav태그 아래의 div태그들을 수평정렬 및 세로길이 설정 */
.nav-link {
  /* 수평정렬, 5개의 button을 각각 20% width만큼 할당 */
  float: left;
  width: 20%;
  text-align: center;

  /* 세로길이 설정 */
  height: 50px;
  line-height: 50px;
}

/* 하단 네비바의 메뉴를 눌렀을 때 색상 변경*/
.nav-item {
  color: rgba(128, 128, 128, 0.623);
}

/* 하단 네비바의 메뉴를 눌렀을 때 색상 변경*/
.active-nav-item {
  color: black;
}
